import React from 'react';
import Media from 'react-media'
import MainCard from './MainCard';
import Button from '../UI/Button';

const MainSecond = () => {

  const cardsXL = [
    {id:1, title: '', text: '30  '},
    {id:2, title: 'Dashboard', text: '   '},
    {id:3, title: 'Skype ', text: '   CRM '},
    {id:4, title: '35 ', text: ' CRM'},
  ]
  const cardsSM = [
    {id:1, title: 'Skype '},
    {id:2, title: 'Dashboard'},
    {id:3, title: '30 '},
    {id:4, title: ' moCRM'},
  ]
  
  return (
    <div className='main-second'>
      <p className="main-second__title text__left_big">
        <span className='text-color'> </span>  :
      </p>
      <div className="main-second__cards">
        <Media queries={{ small: { maxWidth: 1050 } }}>
          {matches => 
            matches.small ? (
              cardsSM.map((card) =>
                <MainCard
                  title={card.title}
                  text=''
                  key={card.id}/>
                ))
            : (
              cardsXL.map((card) =>
                <MainCard
                  title={card.title}
                  text={card.text}
                  key={card.id}/>
                )
            )
          }
        </Media>
        </div>
        <Button />
    </div>
  );
};

export default MainSecond;